@import '../../shared-styles/_colors.scss';
@import '../../shared-styles/mixins';

nav {
  height: 80px;
  border-bottom: 2px solid #e0e0e0;
}

.container {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @include for-size(desktop) {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 15px;
  }
  @include for-size(desktop) {
    // padding-top: 40px;
  }
}

.navContainer {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  @include for-size(tablet) {
    align-items: flex-end;
  }
  @include for-size(desktop) {
    align-items: center;
  }
}

.navContainer a {
  font-family: GothamPro-Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $secondary-text;
  text-decoration: none;
}
.login {
  margin-right: 15px;
}

.navContainer a :hover {
  color: $main-text;
}

.active > span {
  color: $main-text;
}



@media (min-width: 1100px) {
  nav {
    padding-top: 20px;
    height: 140px;
    border-bottom: none;
  }

}
